.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  text-align: center;
}

.wrapper > * {
  margin-top: 5px;
  padding: 10px;
}

.footer {
  background: #f4f4f4;
}

.fieldGroup {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}
.field {
  width: 33%;
}

.main {
  margin-left: 5px;
  text-align: left;
  background: #f4f4f4;
}

.aside-1 {
  background: #f4f4f4;
}

@media all and (min-width: 600px) {
  .aside { -ms-flex: 1 1 auto; flex: 1 1 auto; }
}

@media all and (min-width: 800px) {
  .main    { -ms-flex: 3 1; flex: 3 1; }
  .aside-1 { -ms-flex-order: 1; order: 1; }
  .main    { -ms-flex-order: 2; order: 2; }
  .footer  { -ms-flex-order: 3; order: 3; }
}
.md-22 {
  font-size: 22px;
}


